.Upload-button-label{
    cursor: pointer;
    border-radius: 4px;
    border-style: solid;
    border-width: 1px   ;
    border-color:rgb(211, 208, 208);
    font-weight: 500;
    /* background-color: teal; */
    justify-content: center;
    align-items: center;
    width: 10px;
    padding:12px;
    padding-top: 2px;
    padding-bottom: 5px;
}

.Upload-button-label:hover{
  background-color: rgb(238, 234, 234);
  /* background-color: darkorchid; */
}

input{
    height: 0;
    width: 0;
    opacity: 0;
  }

  
  @media ( width:768px)
and (height:1024px){
  .Upload-button-label:hover{
    pointer-events: none;
    background-color: rgb(238, 234, 234);
    /* background-color: darkorchid; */
  }
}