.NavBar {
    /* background-color: cyan; */
    background-color: white;
    position: fixed;
    top: 0;
    width: 100%;
    height: 54px;
    z-index: 100;
    border-style: solid;
    border-width: 0 0 1px 0  ;
    border-color:rgb(211, 208, 208);
  }

  .Heading {
    font-family: 'Grand Hotel', cursive;
    font-size: 26px ;
    /* position: absolute; */
    -webkit-transform: translateX(20vw) translateY(7px);
            transform: translateX(20vw) translateY(7px);
    display: inline-block;
    font-weight: 600;   
}

#search{
    size: 10px;
    z-index: 1000;
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
}

@media(max-width : 800px){
